const c12 = () => {
  const canvas = document.getElementById("c12");
  const ctx = canvas.getContext("2d");
  
  // —Ä–æ–∑–º—ñ—Ä–∏ –∫–∞–Ω–≤–∞—Å–∞
  const canvasWidth = 400;
  const canvasHidth = 200;

  // —ñ–Ω—ñ—Ç –ª—ñ–Ω—ñ–π
  let lines =  new Array;
  // —Å—Ç–∞–Ω –Ω–∞—Ö–∂–∞—Ç–æ—Å—Ç—ñ
  let pressed = false;

  // –∫–æ–ª—ñ—Ä –∫—Ä—É–∂–∫–∞
  const color = 'green'

  
  var ball = {
    // –ø–æ –∑–∞–º–æ–≤—á—É–≤–∞–Ω–Ω—é x, y —Å—Ö–æ–≤–∞–Ω—ñ –¥–µ—Å—å –∑–≤–µ—Ä—Ö—É
    x:-10,
    y:0,
    // —à–≤–∏–¥–∫—ñ—Å—Ç—å 1, 2, 3
    speed:1,
    // —ñ–Ω–¥–µ–∫—Å —Ç–æ—á–æ–∫ –º–∞—Å–∏–≤–∞ –¥–æ —è–∫–æ–≥–æ —Ä—É—Ö–∞—î—Ç—å—Å—è
    moveTo: 0,
    // –º–∞–ª—é–≤–∞–Ω–Ω—è –∫—Ä—É–∂–∫–∞
    draw: function(){
      if(pressed) return;
      ctx.fillStyle = color;
      ctx.beginPath();
      ctx.arc(this.x,this.y,10,0,Math.PI*2);
      ctx.closePath();
      ctx.fill();
    },

    changePosition: function(){
      console.log('lines', lines);
      // —è–∫—â–æ —Ç–æ—á–æ–∫ –º–µ–Ω—â–µ 2 —Ç–æ —Å—Ç–æ–ø–∞—é
      if(lines.length < 2  || pressed) {
        
        return
      };

    if(this.moveTo >= lines.length) this.moveTo = 0;

    if(this.moveTo == 0) {
      this.x = lines[0].x;
      this.y = lines[0].y;
      this.moveTo++;
    } else {
      //–í–µ—Ä–Ω–µ—Ç —É–≥–æ–ª –≤ —Ä–∞–¥–∏–∞–Ω–∞—Ö –º–µ–∂–¥—É –∫–æ–æ—Ä–¥–∏–Ω–∞—Ç–∞–º–∏ —Ü–µ–Ω—Ç—Ä–∞ –∫—Ä—É–≥–∞ –∏ —Å–ª–µ–¥—É—é—â–µ–π —Ç–æ—á–∫–æ–π –Ω–∞ –ª–∏–Ω–∏–∏
      var angle = Math.atan2(lines[this.moveTo].x - this.x,lines[this.moveTo].y - this.y);

      if( Math.abs(this.x - lines[this.moveTo].x) < this.speed && Math.abs(this.y - lines[this.moveTo].y) < this.speed){
          this.x = lines[this.moveTo].x;
          this.y = lines[this.moveTo].y;
          this.moveTo++;
      } else {
          this.x += Math.sin(angle) * this.speed;
          this.y += Math.cos(angle) * this.speed;
        }    
      }
    }
};



 
function EVM_down(e){
  lines = new Array;
  ball.moveTo = 0;
  var x = e.clientX - canvas.offsetLeft;
  var y = e.clientY - canvas.offsetTop;
  lines.push({x:x,y:y});
  pressed = true;
}

function EVM_move(e){
  if(!pressed) return;
  var x = e.clientX - canvas.offsetLeft;
  var y = e.clientY - canvas.offsetTop;
  lines.push({x:x,y:y});
 }
 
 function EVM_up(e){
   if(!pressed) return;
    var x = e.clientX - canvas.offsetLeft;
    var y = e.clientY - canvas.offsetTop;
    lines.push({x:x,y:y});
    pressed = false;
 }
 
 function handler(){
  ctx.fillStyle = "transparent";
  ctx.clearRect(0, 0, canvas.width, canvas.height);
  ctx.fillRect(0,0,canvasWidth, canvasHidth);
  if(lines.length > 1){
    ctx.beginPath();
    ctx.moveTo(lines[0].x,lines[0].y);
    for(var i = 1; i < lines.length; i++) 
      ctx.lineTo(lines[i].x,lines[i].y);
     ctx.stroke();
     ctx.closePath();
  }

  ball.draw();
  ball.changePosition();
  setTimeout(handler,1000/60);
 }


  canvas.addEventListener("mousedown", EVM_down);
  canvas.addEventListener("mouseup", EVM_up);
  canvas.addEventListener("mousemove", EVM_move);

  // –∑–∞–ø—É—Å–∫ —Ö–µ–Ω–¥–ª–µ—Ä–∞
  handler();

}; // c12

export default c12;
